import { getNextConsecutiveSymbols, getCountOfSymbolsInSelection } from '../';

const getCursorPositionAfterKeystroke = (
  key,
  selectionStart,
  selectionEnd,
  pattern,
  pastedLength = 0,
) => {
  let cursorPosition = selectionStart;

  switch (key) {
    case 'Backspace':
      // If not multiple delete.
      if (selectionStart === selectionEnd) {
        cursorPosition -= getNextConsecutiveSymbols(selectionStart, pattern, 'reverse');
        cursorPosition = cursorPosition > 0 ? cursorPosition - 1 : 0;
      }
      break;

    case 'Paste':
      cursorPosition +=
        pastedLength +
        getCountOfSymbolsInSelection(selectionStart, selectionStart + pastedLength, pattern) +
        getNextConsecutiveSymbols(selectionStart + pastedLength, pattern);
      break;

    case 'Cut':
    case 'Delete':
      break;

    default:
      cursorPosition += 1 + getNextConsecutiveSymbols(selectionStart, pattern);
      break;
  }

  return cursorPosition;
};

export default getCursorPositionAfterKeystroke;



